<template>
  <tas-base-crud :key="mainKey" :config="config">
    <template v-slot:list-action>
      <button :disabled="isLoadingSync" v-if="$_sys.isAllowed(config.permission.create)" @click="syncData()" class="btn btn-primary btn-circle ml-3">
        <i class="ri-refresh-line p-0"></i> Sync Data </button>
      <b-toast id="export-toast" variant="info" solid no-auto-hide no-close-button :visible="isLoadingSync">
        <template #toast-title>
          <div class="d-flex flex-grow-1 align-items-center pt-2">
            <b-spinner label="Spinning" small></b-spinner>
            <strong class="mr-auto pl-3"> Sync data with Taptap ...</strong>
            <small class="text-muted mr-2">
              <time-ago locale="id"></time-ago>
            </small>
          </div>
        </template> Sedang menyinkronkan data {{config.title}} dengan aplikasi Taptap. Harap tunggu sebentar.
      </b-toast>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-lembur-on-periode-recaps',
  data () {
    return {
      mainKey: 0,
      isLoadingSync: false,
      config: {
        title: 'Rincian Rekapitulasi Lembur',
        model_api: 'lembur-on-periode-recaps',
        getter: 'lembur-on-periode-recaps',
        setter: 'lembur-on-periode-recaps',
        pk_field: null,
        permission: {
          create: 'create-lembur-on-periode-recaps',
          read: 'view-lembur-on-periode-recaps',
          update: 'update-lembur-on-periode-recaps',
          delete: 'delete-lembur-on-periode-recaps'
        },
        custom_api: {
          create: 'custom/lembur-on-periode-recaps'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'lembur_on_periode_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { list: { show: false }, create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'duration', label: 'Durasi Lembur', methods: { list: { transform: 'suffix-Jam' }, detail: { transform: 'suffix-Jam' }, create: { type: 'number', attr: [{ precision: 1 }] }, update: { type: 'number', attr: [{ precision: 1 }] }, filter: false } },
          { id: 'data_source', label: 'Sumber Data', methods: { list: { transform: 'uppercase' }, detail: { transform: 'uppercase' }, create: false, update: false, filter: false } },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          // {
          //   id: 'status_code',
          //   label: 'Sinkronisasi',
          //   methods: {
          //     list: {
          //       order: true,
          //       class: { 'not_synchronized_yet': 'badge badge-danger', 'synchronized': 'badge badge-primary' },
          //       transform: 'sync'
          //     },
          //     detail: {
          //       order: true,
          //       class: { 'not_synchronized_yet': 'badge badge-danger', 'synchronized': 'badge badge-primary' },
          //       transform: 'sync'
          //     },
          //     create: false,
          //     update: false
          //   }
          // },
          {
            id: 'type',
            label: 'Tipe Lembur',
            methods: {
              list: { transform: 'holiday' },
              detail: { transform: 'holiday' },
              create: {
                view_data: 'type',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Hari Libur', code: 'holiday' }, { label: 'Hari Kerja', code: 'weekday' }]
                  }
                }
              },
              update: {
                view_data: 'type',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Hari Libur', code: 'holiday' }, { label: 'Hari Kerja', code: 'weekday' }]
                  }
                }
              },
              filter: {
                view_data: 'type',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Hari Libur', code: 'holiday' }, { label: 'Hari Kerja', code: 'weekday' }]
                  }
                }
              }
            }
          },
          { id: 'lembur_description', label: 'Deskripsi Lembur', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'date', label: 'Tanggal', methods: { list: { transform: 'longDate' }, detail: { transform: 'longDate' }, create: { type: 'date' }, update: { type: 'date' }, filter: false } },
          { id: 'start_time', label: 'Mulai dari', methods: { list: { transform: 'longDateTime' }, detail: { transform: 'longDateTime' }, create: { type: 'datetime' }, update: { type: 'datetime' }, filter: false } },
          { id: 'end_time', label: 'Sampai dengan', methods: { list: { transform: 'longDateTime' }, detail: { transform: 'longDateTime' }, create: { type: 'datetime' }, update: { type: 'datetime' }, filter: false } }
        ]
      }
    }
  },
  methods: {
    syncData () {
      this.isLoadingSync = true
      let formData = {
        id: this.$route.params.id
      }
      this.$_api.post('custom/lembur-on-periode-recaps/do-sync-all', formData).then(res => {
        this.isLoadingSync = false
        this.mainKey++
      }).catch((err) => {
        this.isLoadingSync = false
        this.$_alert.error(err)
      })
    }
  }
}
</script>



